.morph-button {
  position: absolute;
  display: block;
  margin: 0 auto;
}
.morph-content {
  pointer-events: none;
}
.morph-button.open .morph-content {
  pointer-events: auto;
}
.morph-button-fixed,
.morph-button-fixed .morph-content {
  width: 60px;
  height: 60px;
}
.morph-button-fixed .morph-content {
  position: fixed;
  z-index: 900;
  opacity: 0;
  -webkit-transition: opacity 0.3s 0.5s, width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, margin 0.4s 0.1s;
  transition: opacity 0.3s 0.5s, width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, margin 0.4s 0.1s;
}
.morph-button-fixed.open .morph-content {
  opacity: 1;
}
.morph-button-fixed .morph-content > div {
  visibility: hidden;
  height: 0;
  opacity: 0;
  -webkit-transition: opacity 0.1s, visibility 0s 0.1s, height 0s 0.1s;
  transition: opacity 0.1s, visibility 0s 0.1s, height 0s 0.1s;
}
.morph-button-fixed.open .morph-content > div {
  visibility: visible;
  height: 100%;
  opacity: 1;
  -webkit-transition: opacity 0.3s 0.5s;
  transition: opacity 0.3s 0.5s;
}
.morph-button-fixed.active > button {
  z-index: 2000;
}
.morph-button-fixed.active .morph-content {
  z-index: 1900;
}
.morph-button-overlay .morph-content,
.morph-button-sidebar .morph-content {
  -webkit-transition: opacity 0.3s 0.5s, width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, border-radius 0.3s, background 0.3s;
  transition: opacity 0.3s 0.5s, width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, border-radius 0.3s, background 0.3s;
}
.morph-button-overlay.open .morph-content,
.morph-button-sidebar.open .morph-content {
  -webkit-transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, border-radius 0.3s, background 0.3s;
  transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, border-radius 0.3s, background 0.3s;
}
.morph-button-overlay .morph-content {
  overflow: hidden;
  background: #fff;
}
.active .morph-content {
  background: #ffdb5a;
  color: #222;
}
.morph-button-overlay.open .morph-content {
  top: 0 !important;
  left: 0 !important;
  width: 100vw;
  height: 100vh;
}
.morph-button-modal::before {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 800;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  pointer-events: none;
}
.morph-button-modal.open::before {
  opacity: 1;
  pointer-events: auto;
}
.morph-button-modal.active::before {
  z-index: 1800;
}
.morph-button-modal .morph-content {
  overflow: hidden;
  -webkit-transition: opacity 0.3s 0.5s, width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, margin 0.4s 0.1s, border-radius 0.3s, background 0.3s;
  transition: opacity 0.3s 0.5s, width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, margin 0.4s 0.1s, border-radius 0.3s, background 0.3s;
}
.morph-button-modal.open .morph-content {
  top: 50% !important;
  left: 50% !important;
  margin: -210px 0 0 -300px;
  width: 600px;
  height: 420px;
  -webkit-transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, margin 0.4s 0.1s, border-radius 0.3s, background 0.3s;
  transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s, margin 0.4s 0.1s, border-radius 0.3s, background 0.3s;
}
.morph-button.morph-button-modal-1 {
  float: left;
}
.morph-button.morph-button-modal-2,
.morph-button.morph-button-modal-3 {
  display: inline-block;
  margin: 10px 15px;
}
.morph-button-modal-1 > button,
.morph-button-modal-1 .morph-content {
  background-color: #553445;
}
.morph-button-modal-2 > button,
.morph-button-modal-2 .morph-content,
.morph-button-modal-3 > button,
.morph-button-modal-3 .morph-content {
  background-color: #fef0e3;
  color: #e75854;
}
.morph-button-modal-4 {
  display: inline-block;
}
.morph-button-modal-4 > button,
.morph-button-modal-4 .morph-content {
  background-color: #faf1e0;
  color: #553445;
}
.morph-button-modal-4 > button span,
.morph-button-modal-4 .morph-clone {
  padding-left: 10px;
  color: #286f81;
}
.morph-button-modal-4 .morph-clone {
  position: absolute;
  right: 34px;
  bottom: 30px;
  z-index: 100;
  letter-spacing: 1px;
  font-weight: 700;
  -webkit-transition: bottom 0.4s 0.1s, right 0.4s 0.1s;
  transition: bottom 0.4s 0.1s, right 0.4s 0.1s;
}
.morph-button-modal-4.open .morph-clone,
.no-js .morph-button-modal-4 .morph-clone {
  right: 10px;
  bottom: 10px;
}
.morph-button-modal-1::before {
  background: rgba(240,221,204,0.7);
}
.morph-button-modal-2.open .morph-content {
  margin: -210px 0 0 -170px;
  width: 340px;
  height: 420px;
}
.morph-button-modal-3.open .morph-content {
  margin: -255px 0 0 -210px;
  width: 420px;
  height: 510px;
}
.morph-button-modal-3.open .morph-content > div {
  height: 420px;
}
.morph-button-modal-2.open .morph-content > div,
.morph-button-modal-3.open .morph-content > div {
  -webkit-transition: opacity 0.3s 0.3s;
  transition: opacity 0.3s 0.3s;
}
.morph-button-modal-4.open .morph-content {
  margin: -200px 0 0 -320px;
  width: 640px;
  height: 400px;
}
.morph-button-inflow {
  overflow: hidden;
  max-width: 100%;
  height: 70px;
}
.morph-button-inflow > button {
  width: 100%;
  line-height: 70px;
}
.morph-button-inflow .morph-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.morph-button-inflow .morph-content .morph-clone {
  padding: 0;
  font-weight: 700;
  font-size: 1.5em;
  line-height: 70px;
}
.morph-button-inflow-1 {
  width: 600px;
  margin: 2em auto;
  -webkit-transition: height 0.5s cubic-bezier(0.7, 0, 0.3, 1);
  transition: height 0.5s cubic-bezier(0.7, 0, 0.3, 1);
}
.morph-button-inflow-1 > button span {
  visibility: hidden;
}
.morph-button-inflow-1 .morph-content .morph-clone {
  color: #f9f6e5;
  background: #e85657;
}
.morph-button-inflow-2 {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 220px;
  background-color: #fef0e3;
  -webkit-transition: height 0.3s, width 0.3s, -webkit-transform 0.3s;
  transition: height 0.3s, width 0.3s, transform 0.3s;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
.morph-button-inflow-2 > button {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  color: #e75854;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}
.morph-button-inflow-2.open > button {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}
.morph-button-inflow-2 .morph-content {
  width: 260px;
  height: 200px;
}
.morph-button-inflow-2.open {
  width: 260px;
}
.morph-button-sidebar,
.morph-button-sidebar .morph-content {
  width: 60px;
  height: 60px;
}
.morph-button-sidebar {
  position: fixed;
  bottom: 50px;
  left: 50px;
}
.morph-button-sidebar > button {
  line-height: 60px;
  font-size: 1.6em;
  padding: 0;
}
.morph-button-sidebar .morph-content {
  background: #e85657;
}
.morph-button-sidebar.open .morph-content {
  top: 0 !important;
  left: 0 !important;
  width: 300px;
  height: 100%;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
}
.morph-button .morph-content,
.morph-button.open .morph-content,
.morph-button-modal-4 .morph-clone {
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}
.noscroll {
  overflow: hidden;
}
.morph-content {
  border-radius: 50%;
}
.open .morph-content {
  border-radius: 0;
}
.no-js .morph-button > button {
  display: none;
}
.no-js .morph-button {
  margin: 10px 0;
  float: none;
}
.no-js .morph-button,
.no-js .morph-button .morph-content,
.no-js .morph-button .morph-content > div {
  position: relative;
  width: auto;
  height: auto;
  opacity: 1;
  visibility: visible;
  top: auto;
  left: auto;
  -webkit-transform: none;
  transform: none;
  pointer-events: auto;
}
.no-js .morph-button .morph-content .icon-close {
  display: none;
}
.no-js .morph-button-sidebar {
  width: 300px;
  position: fixed;
  top: 0;
  left: 0;
  margin: 0;
  height: 100%;
  background: #e85657;
  overflow: auto;
}
.no-transition {
  -webkit-transition: none !important;
  transition: none !important;
}
@media screen and (max-width: 600px) {
  .morph-button-modal.open .morph-content {
    top: 0% !important;
    left: 0% !important;
    margin: 0;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    -webkit-transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s;
    transition: width 0.4s 0.1s, height 0.4s 0.1s, top 0.4s 0.1s, left 0.4s 0.1s;
  }
}
@media screen and (max-width: 400px) {
  .morph-button-fixed > button {
    font-size: 75%;
  }
  .morph-button-sidebar > button {
    font-size: 1.6em;
  }
  .morph-button-inflow .morph-content .morph-clone {
    font-size: 0.9em;
  }
  .morph-button-modal-4,
  .morph-button-modal-4 .morph-content {
    width: 220px;
    height: 120px;
  }
  .morph-button-modal-4 > button {
    font-size: 100%;
    line-height: 50px;
  }
  .morph-button-modal-4 > button span {
    display: block;
  }
  .morph-button-modal-4 .morph-clone {
    right: 83px;
    bottom: 26px;
  }
  .morph-button-sidebar,
  .morph-button-sidebar .morph-content {
    width: 100% !important;
    height: 60px !important;
  }
  .morph-button-sidebar {
    bottom: 0px;
    left: 0px;
  }
  .morph-button-sidebar.open .morph-content {
    height: 100% !important;
  }
}
.menu {
  width: 300px;
  height: 200px;
  position: fixed;
  top: auto;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
  filter: url("../menu.html#goo");
  transform: translateZ(0);
}
.menu-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
}
.menu-toggle-button,
.menu-item-bounce,
.menu-item-button {
  color: #000;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin-top: -40px;
  margin-left: -40px;
  border: none;
  outline: none;
  position: relative;
  padding: 0;
}
.menu-toggle-button,
.menu-item-button {
  cursor: pointer;
}
.menu-toggle-button,
.menu-item-bounce {
  background: #fff;
}
.menu-toggle-button {
  background: transparent;
  position: relative;
  top: 0;
  left: 0;
}
.menu-toggle-button img {
  border-radius: 50%;
  overflow: hidden;
}
.menu-toggle-icon {
  font-size: 30px;
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 80px;
  line-height: 80px;
}
.menu-items {
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.menu-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
}
.menu-item-bounce {
  position: absolute;
  top: 0;
  left: 0;
}
img.menu-item-icon {
  width: 100%;
  max-width: 100%;
  border-radius: 50%;
}
.menu-item-button {
  width: 60px;
  height: 60px;
  margin-top: -30px;
  margin-left: -30px;
  position: absolute;
  top: 0;
  left: 0;
  color: #ad4c4c;
}
.menu-item-button:before {
  content: '';
  background: #f59393;
  width: 0;
  height: 0;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: all 1s ease-in-out;
  transform: translate(-50%, -50%);
}
.menu-item-button.is-active {
  width: 100vw;
  height: 100vh;
}
.menu-item-button:hover {
  color: #fff;
}
body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  margin: 0;
  background-size: cover;
  background-attachment: fixed;
  font-size: 15px;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  background: #222;
}
* {
  box-sizing: border-box;
}
figure {
  margin: 0;
}
.btn {
  padding: 15px;
}
/* helper class when showing content */
.noscroll {
  overflow: hidden;
}
.u-bg--violet {
  background-color: #433b8f;
}
.u-bg--orange {
  background-color: #f07502;
}
.u-bg--green {
  background-color: #7cbc19;
}
.u-bg--blue {
  background-color: #0184cd;
}
.u-bg--red {
  background-color: #f44336;
}
.u-bg--pink {
  background-color: #ff4081;
}
h1 {
  font-weight: 100;
  font-size: 45px;
  position: absolute;
  top: 40%;
  left: 0;
  right: 0;
  text-align: center;
}
h1 small {
  font-size: 0.5em;
  display: block;
}
.toggleMenu {
  background: #f07502;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #fff;
  text-align: center;
  line-height: 48px;
  font-size: 25px;
  border: none;
  position: absolute;
  top: 55%;
  left: 0;
  right: 0;
  margin: auto;
}
.main {
  position: relative;
}
/* Overlay */
.grid::after {
  content: '';
  background: rgba(202,202,202,0.4);
  opacity: 0;
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.view-single .grid::after {
  opacity: 1;
}
.morph {
  width: 100%;
  transform: translate3d(0, 0, 0);
  will-change: left, top, height, width, transform;
  left: 0;
  top: 0;
  transition: all 0.3s ease;
  background: #808080;
}
#scene {
  perspective: 150px /* the lower this is, the fancier it gets */;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#emitter {
  position: absolute;
  left: 50%;
  top: 50%;
  transform-style: preserve-3d;
}
.particle {
  position: absolute;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 20%;
}
.ss-wrapper {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  float: left;
}
.ss-content {
  height: 100%;
  width: 100%;
  padding: 0 32px 0 0;
  position: relative;
  right: -18px;
  overflow: auto;
  box-sizing: border-box;
}
.ss-scroll {
  position: relative;
  background: rgba(0,0,0,0.1);
  width: 9px;
  border-radius: 4px;
  top: 0;
  z-index: 2;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.25s linear;
}
.ss-hidden {
  display: none;
}
.ss-container:hover .ss-scroll {
  opacity: 1;
}
.ss-grabbed {
  -o-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
